<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示所有交易成功的订单</li>
      <li>可对已回复的内容进行修改</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>订单号</label>
        <el-input v-model="queryParams.orderCode" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>订单评价状态</label>
        <el-select v-model="queryParams.commentStatus" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="已评价" :value="2"></el-option>
          <el-option label="双方已评" :value="4"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>评价时间</label>
        <cms-picker v-model="timer" @change="setTimer" ></cms-picker>
      </div>
      <div class="query-group">
        <label>商品评价</label>
        <el-select v-model="queryParams.status" @change="query">
          <el-option label="全部" value=""></el-option>
          <el-option label="好评" :value="1"></el-option>
          <el-option label="中评" :value="2"></el-option>
          <el-option label="差评" :value="3"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 数据列表 -->
    <table class="comment-table" v-loading="loading">
      <tbody v-for="(item,index) in tableData" :key="index"
       :style="{'margin-top':index!=0?'15px':'0'}">
        <tr class="comment-th">
          <td style="padding-left:10px;">
            <div>
              <span class="comment-status">
               {{item.order.commentStatus==2?'已评价':'双方已评'}}
              </span>
              <span>{{item.orderCode}}</span>
            </div>
          </td>
          <td class="comment-time">
            <span style="margin-left:10px;">商品评价</span>
            <p class="comment-user" style="width:60%;padding-right:10px;">
              <span>评价人：{{item.memberName}}</span>
              <span>评论时间：&nbsp;  {{$moment(item.createTime)}}</span>
            </p>
          </td>
        </tr>
        <tr style="border:1px solid #EBEEF5;" v-for="(info,indexs) in item.commentdList" :key="indexs">
          <td class="comment-goods" :style="{'border-right':info.storeContent?'1px solid #EBEEF5':'0'}">
            <!-- 商品信息 -->
            <div class="comment__inner">
              <img class="product-img" :src="$cms.filterUrl(info.product.coverImage)">
              <div class="product-title">
                <h3 class="comment-title-h">{{info.product.name}}</h3>
                <p class="title-p" style="max-width:340px;" v-if="info.productSkuName">
                  规格:{{info.productSkuName}}
                </p>
              </div>
            </div>
         </td>
         <td class="comment-item" style="width:50%;">
           <!-- 商品评价 -->
           <div class="comment-remark" :style="{'border-bottom':info.storeContent?'1px solid #EBEEF5':'0'}"
           style="display:flex;">
             <div style="width:35%;">
              <el-rate v-model="info.matchV" disabled class="starts-rate"
              :show-text="true" :texts="starsText">
              </el-rate>
               <p class="comment-title" :class="showColors(info.matchV)" >
                 <span>{{showFirst(info.content)}}</span>
                 <transition name="el-collapse-transition">
                   <span v-show="info.showContent">{{showLeft(info.content)}}</span>
                  </transition>
                 <span class="show-more" v-if="info.content.length>100&&!info.showContent"
                  @click="toggleShow(info,1)">
                  查看更多
                 </span>
                  <span class="show-more" v-if="info.showContent"
                  @click="toggleShow(info,1)">
                  隐藏更多
                 </span>
               </p>
             </div>
             <div style="width:65%;">
               <div class="comment-imgs">
                <thumb-img :data="info.spaceDataList"></thumb-img>
               </div>
             </div>
           </div>
           <div class="comment-remark" v-if="info.storeContent">
             <div style="width:37%">
               <p style="font-size:14px;">您的回复:</p>
                <p class="comment-title">
                 <span >{{showFirst(info.storeContent)}}</span>
                 <span v-show="info.showStoreContent">{{showLeft(info.storeContent)}}</span>
                 <span class="show-more" v-if="info.storeContent.length>100&&!info.showStoreContent"
                  @click="toggleShow(info,2)">
                  查看更多
                 </span>
                  <span class="show-more" v-if="info.showStoreContent"
                  @click="toggleShow(info,2)">
                  隐藏更多
                 </span>
               </p>
               <p style="margin-top:8px;">{{$moment(info.updateTime)}}</p>
             </div>
           </div>
         </td>
        </tr>
        <!-- 总体评价 -->
        <tr class="comment-all">
          <td colspan="2">
            <div style="display: flex;justify-content:space-between;">
              <div style="display: flex;margin-left:10px;align-items:center;">
                <span style="cursor:pointer;user-select:none"  @click="toggle(index)">店铺评价
                  <i class="el-icon-arrow-right commnet-arrow"></i>
                </span>

                <div class="comment-stars" v-show="item.isShow">
                  <label class="stars-label">商品符合度:</label>
                  <el-rate v-model="item.matchV" disabled class="starts-rate" :show-text="true"
                   :texts="starsText">
                  </el-rate>
                  <label class="stars-label">发货速度:</label>
                  <el-rate v-model="item.express" disabled class="starts-rate" :show-text="true"
                   :texts="starsText">
                  </el-rate>
                  <label class="stars-label">服务态度:</label>
                  <el-rate v-model="item.service" disabled class="starts-rate" :show-text="true"
                   :texts="starsText">
                  </el-rate>
                </div>
              </div>
              <div style="margin-right:10px;">
                <cms-button size="small" :colorPicker='true' v-if="item.order.commentStatus==4"
                 @click="$routerLink('/comment/update','update',{id:item.id})">修改回复
                </cms-button>
                <cms-button size="small" :colorPicker='true' v-if="item.order.commentStatus==2"
                @click="$routerLink('/comment/update','update',{id:item.id})">
                 发表回复
                </cms-button>
              </div>
            </div>
          </td>
        </tr>
        <tr>
          <td colspan="2" style="height:20px;"></td>
        </tr>
      </tbody>
    </table>
    <div class="comment-empty" v-if="tableData.length==0">暂无数据</div>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import thumbImg from './thumbImg'
import { commentPage } from '@/api/customer/customer.js'
var defaultData = {
  'orderCode': '',
  'commentStatus': '',
  'evaluateStartTime': '',
  'evaluateEndTime': '',
  'status': '',
  'page': 1,
  'size': 10
}
export default {
  components: {
    thumbImg
  },
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      tableData: [],
      total: 0,
      timer: [],
      starsText: ['差评', '差评', '中评', '好评', '好评'],
      max: 100// 控制初次显示评论信息的长度
    }
  },
  methods: {
    getTableData() {
      this.loading = true
      commentPage(this.queryParams).then(res => {
        if (res.code === 200) {
          res.data.content.forEach(v => { // 添加一条属性控制店铺评价的显示
            v.isShow = false
            v.commentdList.forEach(k => {
              k.showContent = false
              k.showStoreContent = false
            })
          })
          this.tableData = res.data.content
          this.total = res.data.totalElements
          this.loading = false
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    showFirst(target) { // 显示首次评论信息的长度
      return target.substr(0, this.max)
    },
    showLeft(target) { // 显示剩余评论信息的长度
      return target.substring(this.max)
    },
    toggleShow(info, type) {
      if (type === 1) { // 控制买家评论
        this.$set(info, 'showContent', !info.showContent)
      } else { // 控制商家评论
        this.$set(info, 'showStoreContent', !info.showStoreContent)
      }
    },
    showColors(val) { // 根据不同评分值来显示字体的颜色
      if (val <= 2) {
        return 'fontRed'
      } else if (val === 3) {
        return 'fontBlue'
      } else {
        return ''
      }
    },
    toggle(index) { // 控制店铺管理
      this.$set(this.tableData[index], 'isShow', !this.tableData[index].isShow)
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.evaluateStartTime = timer[0]
      this.queryParams.evaluateEndTime = timer[1]
      this.query()
    }

  },
  created() {
    this.getTableData(commentPage, this.queryParams)
  }
}
</script>
<style scoped>
.comment-table{
  padding: 0 10px;
}
.comment-goods{
  width: 40%;
  padding: 10px 0;
  vertical-align: top;
}
.comment-item{
  width: 60%;
  vertical-align: top;
}
.comment-user{
  min-width: 50%;
}

</style>



